<template>
  <div class="event-consecutive event-start event-end" 
        v-popover:pop
        :data-span="period">
    {{ title }}
    <popover name="pop" :width="200">
      <div class="content-line">
        <div class="event-consecutive-marking">
        </div>
        <div class="title">
          <h5>일정 1</h5>
          <h5 class="reservation">2019년 9월 15일 – 17일</h5>
        </div>
      </div>
      <div class="content-line">
        <i class="material-icons">notes</i>
      </div>
      <div class="title">
        <h5 class="reservation">스케쥴 메모, 설명이 나타납니다.</h5>
      </div>
    </popover> 
  </div>
</template>

<script>
export default {
  name: 'Consecutive',
  props: {
    id: Number,
    title: String,
    period: Number
  }
}
</script>

<style>
div[data-popover="pop"] {
  padding: 16px;
  color: #263747; }
  div[data-popover="pop"] .content-line {
      display: flex;
      align-items: flex-start; }
    div[data-popover="pop"] .content-line .event-consecutive-marking {
        margin-top: 10px;
        width: 24px;
        height: 24px; 
        border-radius: 4px;
        background-color: #0078FF; }
    div[data-popover="pop"] .content-line .title {
        margin-left: 12px; }
      div[data-popover="pop"] .content-line .title .reservation {
          display: block; }
    div[data-popover="pop"] .content-line .material-icons {
        color: #263747; }
    div[data-popover="pop"] .content-line:nth-child(2) {
        margin-top: 16px; }
</style>



